<template>
  <div class="index">
    <div id="leftList">
      <el-table style="border-radius: 12px" :data="list"
                :show-header="false">
        <el-table-column align="left">
          <!--        <template slot="header">
                    排序：默认
                  </template>-->
          <span class="label">tag</span>
          <template slot-scope="scope">

            <dl>
              <router-link :key="scope.row.id" :title="scope.row.title"
                           :to="{name: 'show', params: {data:scope.row}}">
                <dt><span class="f-14">{{ scope.row.title }}</span>
                </dt>
                <dd class="desc opacity-8">{{ scope.row.text }}</dd>
              </router-link>
              <dd class="opacity-5 mt-5">
                <span class="mr-10"><i class="el-icon-time mr-2"> {{ scope.row.date }}</i></span>
                <!--              <span class="mr-10"><i class="el-icon-view mr-2"></i> 浏览</span>
                              <span><i class="el-icon-chat-dot-round mr-2"></i> 评论</span>-->
              </dd>
            </dl>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="2">
        </el-pagination>
      </div>
    </div>
    <div id="rightSide">
    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  data() {
    return {
      list: [],
    }
  },
  methods: {
    inShow(args) {
      console.log(args)
      this.$router.push({name: 'show', params: {data: args}})
    },
    async getList() {
      //axios的get请求向后台获取用户信息数据
      const {data: res} = await this.$ajax.get('/test')
      if (res.code === 200) {
        this.list = res.data
        console.log(this.list)
      }
    },
  },
  created() {
    this.getList()
  }
}
</script>

<style scoped>


.index {
  position: relative;
  top: 20px;
  width: 89.3%;
  margin: 0 auto;
  border-radius: 12px;
  display: flex;
}

#leftList {
  width: 75%;
  float: left;
  border-radius: 12px;
  background: white;
}

#rightSide {
  width: 24%;
  background: white;
  position: relative;
  border-radius: 12px;
  left: 1.1rem;
  float: right;
}


el-menu-demo {
  position: absolute;
}


.f-14 {
  font-size: 14px;
}


dl {
  padding: 0 10px;
}

dt {
  color: #428BD1;
  line-height: 22px;
}

dd {
  margin: 0;
  line-height: 22px;
}

dl a {
  font-weight: 500;
}

.page {
  margin-top: 0.6rem;
}

.label {
  line-height: 25px;
  margin-right: 5px;
  padding: 1px 5px;
  color: #fff;
  background: #d9534f;
}

.el-table thead tr th {
  padding: 6px 10px !important;
}

.el-icon-third-recomment2 {
  position: absolute;
  right: 0;
  top: 14px;
  color: #d9534f;
}

.el-icon-third-recomment2:before {
  font-size: 28px;
}
</style>



