import Vue from 'vue'
import Router from 'vue-router'
import Main from "../pages/Main";
import Show from "../components/Show";
import Login from "../pages/Login";
Vue.use(Router)

var app =new Router({
  routes: [
    {
      path: '/',
      component: resolve => require(['../pages/Main'], resolve),
      redirect: 'index',
      children: [
        {
          path: 'index',
          name: 'main',
          component: Main
        }
      ]
    },
    {
      path: '/show',
      name: 'show',
      component: Show
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    }
  ]
})

export default app



